// import Inputmask from "inputmask";

var mask = function(){
	return {
		phoneMask: function(){
			$('.phone-mask').mask('+380(99) 999-9999');
			$('.phone-mask').attr('placeholder',"+380(__) ___-____")
		},
		initialState: function(){
		},
		init: function(){
			this.phoneMask();
			this.initialState();
		}
	}
}
export default mask



