var mobileMenu = function(){
	return {
		clickBurger: function(){
			self = this;
			$('.header__burger').on('click', function(event){
				event.stopPropagation();
				$(this).toggleClass('header__burger--active')
				let hasClass = $('.header__burger').hasClass('header__burger--active')
				if(hasClass){
					self.openMenu();
				} else {
					self.closeMenu();
				}
			})
		},
		openMenu: function(){
			$('#mobileMenu').show()
		},
		closeMenu: function(){
			$('#mobileMenu').hide()
		},
		otherClick: function(){
			$(window).on('click', () => {
				this.closeMenu();
				$('.header__burger').removeClass('header__burger--active')
			})

		},
		init: function(){
			this.clickBurger();
			this.otherClick();
		}
	}
}

export default mobileMenu



